.Home {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  flex-wrap: wrap;

  .card {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 190px;
    height: 190px;
    cursor: pointer;
    font-size: 1em;
    margin: 9px;

    @media screen and (min-width: 768px) {
      width: 300px;
      height: 300px;
      font-size: 2em;
      margin: 29px;
    }
  }
}
