.Daici {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 100vh;

  .container {
    display: flex;
    align-items: flex-end;
    margin: 30px 0;
    position: relative;

    .correctAnswer {
      position: absolute;
      bottom: -150%;
      right: 0;
      display: flex;
      align-items: flex-end;
      opacity: 0;

      &.fadeIn {
        animation: 0.5s fadeIn ease forwards;
      }

      &.fadeOut {
        animation: 0.5s fadeOut ease forwards;
      }
    }
  }
  .input {
    margin: 0 30px;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }

}
