.Pinyin {
  display: flex;
  padding: 50px 10px;
  flex-direction: column;
  min-height: 100vh;
  box-sizing: border-box;

  @media screen and (min-width: 768px) {
    padding: 100px;
  }

  .container {
    display: inline-flex !important;
    align-items: center;
    flex-direction: column;
    flex-grow: 1;

    .input {
      margin: 20px 0;
    }
  }

  .slick-prev {
    left: 9px;
    width: 49px;
    height: 49px;
    z-index: 1;
  }
  .slick-next {
    right: 9px;
    width: 49px;
    height: 49px;
    z-index: 1;
  }
  .slick-next:before, .slick-prev:before {
    color: grey;
    font-size: 49px;
    z-index: 1;
  }

  .slick-dots {
    bottom: -29px;
  }

  .slick-slider {
    display: flex;
    flex-grow: 1;
    align-items: center;
  }

  .slick-slider .slick-list, .slick-slider .slick-track {
    height: 100%;
    width: 100%;
  }
}
